body{
    background-color: rgb(255, 255, 255);
   
}

.App{
    display:flex;
   padding: 16px;
   margin-top: 1rem;
 align-items: center;
    justify-content: center;
}
.calculator{
  overflow: hidden;
  border-radius: 1rem;
  
    width:100%;
    max-width: 300px;
    background-color: white;
    

}
.display{
    background-color: black;
    padding: 16px;
    font-size: 24px;
    text-align: right;
    color: white;
    height:1rem;
}
.display span{
    font-size: 14px;
    color:rgb(172, 175, 177)
}
button{
    appearance: none;
    outline: none;
    border:none;
    font-size: 20px;
    cursor: pointer;
    color: white;
    transition: 0.4s;
    height: 3rem;
}
button:hover{
    opacity: 0.9;
}
.Operators{
    display: flex;
}
.Operators button{
    flex:1 1 0%;
    background-color: rgb(225, 59, 86);
    font-weight: 600;
}
.digits{
    display: flex;
    flex-wrap: wrap;
}
.digits button{
    flex:1 1 33.333%;
    max-width: 33.333%;
    background-color: rgb(25, 24, 24);
}